import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import { DataService } from '../../services/data.service';
import { Globals } from '../../globals/globals';

@Component({
  selector: 'app-help',
  templateUrl: './help.component.html',
  styleUrls: ['./help.component.css']
})
export class HelpComponent implements OnInit {

  rForm: FormGroup;
  message: any;
  subject: string;
  text: string;
  messageSuccess: string;
  isOK = false;
  isNotOK = false;
  alertClass: string;

  constructor(private fb: FormBuilder, private dataService: DataService) {
    this.rForm = fb.group({
      'subject': [null, Validators.compose([Validators.required, Validators.minLength(6)]) ],
      'text': [null, Validators.compose([Validators.required, Validators.minLength(6)]) ]
    });
  }

  ngOnInit() {
  }

  sendMessage(message) {
    this.subject = message.subject;
    this.text = message.text;


    this.dataService.sendMessage(this.subject, this.text, JSON.parse(localStorage.getItem('curentUser')).token).subscribe((success) => {
      if (success === true) {
        this.alertClass = 'alertGreen';
        this.isOK = true;
        this.messageSuccess = 'Message sent successfuly';
        console.log(success);
      }else {
        this.alertClass = 'alertRed';
        this.isOK = true;
        this.messageSuccess = 'Message did not sent successfuly';
      }



     });

  }

}



