import { Component, OnInit } from '@angular/core';
import { Globals } from '../../globals/globals';
import { DataService } from '../../services/data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-userinfo',
  templateUrl: './userinfo.component.html',
  styleUrls: ['./userinfo.component.css']
})
export class UserinfoComponent implements OnInit {
  token:string;
  
  name:string = 'Loading...';
  photoUrl:string = 'http://aiyd.org/wp-content/uploads/2016/09/no-image-icon-hi.png';
  permission:string = 'Loading...';
  email:string = 'Loading...';

  constructor(private dataService:DataService,private router: Router) { }

  ngOnInit() {
    this.token = JSON.parse(localStorage.getItem('curentUser')).token;
    if(this.token == null){
      this.router.navigate(['/login']);
    }else{
      this.dataService.userinfo(this.token).subscribe((user) =>{
       this.photoUrl = user.photoUrl;
        this.name = user.name;
        
        this.permission = user.permissions;
        this.email = user.email;
        
        if(this.photoUrl == null){
          this.photoUrl = "http://aiyd.org/wp-content/uploads/2016/09/no-image-icon-hi.png";
        }

       });
    }
    

   
  

    }
  }

interface User{
  photoUrl:string,
  name:string,
  permissions:string,
  email:string
}





