import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import { Http, Headers, Response, RequestOptions, RequestMethod } from '@angular/http';
import { Observable } from 'rxjs/Observable';


@Injectable()
export class DataService {

  constructor(public http:Http) { 
    console.log('Data service conected...')
  }

  addUser(name:string,password:string,email:string,photoUrl:string){
    var user:any;
    console.log(photoUrl);
    if(password == null){
       user = {name:name,email:email,photoUrl:photoUrl};
      
    }else{
      user = {name:name,password:password,email:email,photoUrl:photoUrl};
    }
     
   return this.http.post('https://swarmnetbackendusa.herokuapp.com/register', user).map(res => res.json());
  }

  login(email:string,password:string){
    var user = {email:email,password:password};
    console.log(user);
   return this.http.post('https://swarmnetbackendusa.herokuapp.com/login', user).map(res => res.json())
   .catch(this._errorHandler);
  }

  _errorHandler(error: Response){
    console.error(error);
    return Observable.throw(error || "Server error");
  }

  getAllSensors(token:string){
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/sensors/my', opts).map(res => res.json());
  }

  dailyWaterData(token:string){
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/readings/last10', opts).map(res => res.json());
  }

  getWaterParameters(token:string,idSensor:number){
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/readings/'+idSensor, opts).map(res => res.json());
  }

  getLastWaterParameters(token:string,idSensor:number,idLastReading:number){
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/readings/'+idSensor+'/'+idLastReading+'/new', opts).map(res => res.json());
  }

  getLogedUserInfo(token:string){
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/userinfo', opts).map(res => res.json());
  }

  verifyToken(email:string,token:string){
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/users/'+email+'/confirm/'+token).map(res => res.json());
  }

  userinfo(token:string){
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/userinfo', opts).map(res => res.json());
  }

  changePassword(token:string,oldPassword:string,newPassword:string){
    var user = {oldPassword:oldPassword,newPassword:newPassword};
    console.log(user);
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.put('https://swarmnetbackendusa.herokuapp.com/changepassword',user, opts).map(res => res.json());
  }
  sendMessage(subject:string,text:string,token:string){
    var message = {subject:subject,text:text};
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
    return this.http.post('https://swarmnetbackendusa.herokuapp.com/contact', message,opts).map(res => res.json());
  }

  getConsumptionDaily(token:string){
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/consumption/daily', opts).map(res => res.json());
  }

  getConsumptionWeekly(token:string){
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/consumption/weekly', opts).map(res => res.json());
  }

  getConsumptionMonthly(token:string){
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/consumption/monthsbefore/0', opts).map(res => res.json());
  }

  getConsumptionYearly(token:string){
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/consumption/yearly', opts).map(res => res.json());
  }

  getUsers(token:string,page:number){
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/users/page/'+page, opts).map(res => res.json());
  }

  showUserDataForChange(token:string,id:number){
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.get('https://swarmnetbackendusa.herokuapp.com/users/'+id, opts).map(res => res.json());
  }

  changeUserData(id:number,username:string,password:string,name:string,email:string,token:string){
    var user = {id:id,username:username,password:password,name:name,email:email};
    let headers = new Headers();
    headers.append('Authorization', 'Bearer ' + token);
    let opts = new RequestOptions();
    opts.headers = headers;
   return this.http.put('https://swarmnetbackendusa.herokuapp.com/users/',user, opts).map(res => res.json());
  }

}



