import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';
import { post } from 'selenium-webdriver/http';
import { Router } from '@angular/router';
import{NgModule} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import { AuthService } from "angularx-social-login";
import { FacebookLoginProvider, GoogleLoginProvider } from "angularx-social-login";
import { SocialUser } from "angularx-social-login";


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  private userAuthout2: SocialUser;
  private loggedIn: boolean;

  rForm: FormGroup;
  user:any;
  name:string = '';
  password:string = '';
  
  surname:string = '';
  email:string='';
  titleAlert:string = 'This field is required';
  emailAlert:string = 'Please enter the correct email, this email not valid';
  isOK:boolean = false;
  isFAILED:boolean = false;
  messageEmail:string = 'Verification email has been sent to your adress!\nCheck your email to complete registration!';
  badmessageEmail:string = 'That email adress does not exist!';

  users:User[];

  constructor(private dataService:DataService,private router: Router,private fb: FormBuilder,private authService: AuthService) { 
    this.rForm = fb.group({
      'name': [null, Validators.compose([Validators.required, Validators.minLength(5)])],
      'password': [null, Validators.compose([Validators.required, Validators.minLength(6)]) ],
      'email': ['', Validators.email]
      
    })
    
  }

  ngOnInit() {
    this.userAuthout2 = null;
   
  }

  addUser(user){
    console.log('Dodavanje usera');
    this.name = user.name;
    this.password = user.password;
    this.email = user.email;
    
    this.dataService.addUser(this.name,this.password,this.email,null).subscribe((success) => {
     if(success == true){
      this.isOK = true;
      this.isFAILED = false;
     }else{
       this.isFAILED = true;
       this.isOK = false;
     }
     
      
    });
  }

  redirectToLogin(){
    
    this.router.navigate(['/login']);
  }

  signInWithGoogle(): void {
    this.authService.signIn(GoogleLoginProvider.PROVIDER_ID);
    this.authService.authState.subscribe((user) => {
      this.userAuthout2 = user;
      this.loggedIn = (user != null);
      var photo = this.userAuthout2.photoUrl;
     
      this.dataService.addUser(this.userAuthout2.name,null,this.userAuthout2.email,photo).subscribe((success) => {
        if(success == true){
         this.isOK = true;
         this.isFAILED = false;
        }else{
          this.isFAILED = true;
          this.isOK = false;
        }
        
         
       });
    });
    console.log(this.userAuthout2);
  }

  signInWithFB(): void {
    this.authService.signIn(FacebookLoginProvider.PROVIDER_ID);
    this.authService.authState.subscribe((user) => {
      this.userAuthout2 = user;
      this.loggedIn = (user != null);
      var photo = this.userAuthout2.photoUrl;
     
      this.dataService.addUser(this.userAuthout2.name,null,this.userAuthout2.email,photo).subscribe((success) => {
        if(success == true){
         this.isOK = true;
         this.isFAILED = false;
        }else{
          this.isFAILED = true;
          this.isOK = false;
        }
        
         
       });
    });
    console.log(this.userAuthout2);
    
  }

  signOut(): void {
    this.authService.signOut();
    this.user = null;
  }

}

interface User{
  photoUrl:string,
  password:string,
  name:string,
  
  email:string
}



