import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';
import { Globals } from '../../globals/globals';
import { Router } from '@angular/router';
import{NgModule} from '@angular/core';
import {UserToken} from '../login/login.component';
import { AuthService } from "angularx-social-login";
import { FacebookLoginProvider, GoogleLoginProvider } from "angularx-social-login";
import { SocialUser } from "angularx-social-login";

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
  token:string;
  user:any;
  name:string;
  
  isAdmin:boolean;
  hideLogo:boolean = false;

  constructor(private dataService:DataService,private router: Router,private authService: AuthService) { }

  ngOnInit() {
    this.token = JSON.parse(localStorage.getItem('curentUser')).token;
    this.dataService.getLogedUserInfo(this.token).subscribe((user) =>{
      this.user = user;
      console.log(user);
      this.name = user.name;
      
    });
  }

  logOut(){
    localStorage.removeItem('curentUser');
    this.router.navigate(['/login']);
    this.authService.signOut();
    this.user = null;

    
  }
  redirectToHome(){
    if(localStorage.getItem('curentUser') != null){

     
     this.isAdmin = JSON.parse(localStorage.getItem('curentUser')).user.permissions.includes('admin');
     if(this.isAdmin){
      this.router.navigate(['/adminPage']);
    }else{
      this.router.navigate(['/userPage']);
    }
      
    }
  }
  redirectToUserInfo(){
    this.router.navigate(['/userinfo']);
  }
  redirectToChangePassword(){
    this.router.navigate(['/changePassword']);
  }
  redirectToAdminPage(){
    this.router.navigate(['/adminPage']);
  }
  redirectToHelp(){
    this.router.navigate(['/help']);
  }


}



