import { Component, OnInit, Input } from '@angular/core';
import {AdminPageComponent} from '../admin-page/admin-page.component'; 
import {ActivatedRoute, Params} from '@angular/router';
import { DataService } from '../../services/data.service';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import { Router } from '@angular/router';


@Component({
  selector: 'app-change-user-data',
  templateUrl: './change-user-data.component.html',
  styleUrls: ['./change-user-data.component.css']
})
export class ChangeUserDataComponent implements OnInit {

  rForm: FormGroup;
  idCustomer:any;
  user:any;
  username:string;
  name:string;
  permission:string;
  email:string;
  password:string;
  isValid:boolean = false;
  errorMessage:string;
  oldName:string;
  oldSurname:string;
  oldEmail:string;
  oldPassword:string;
  classAlert:string;

  constructor(private route: ActivatedRoute,private dataService:DataService,private router: Router) {}
  

  

  ngOnInit() {
    
   this.idCustomer =  this.route.snapshot.params['id'];
   
   this.dataService.showUserDataForChange(JSON.parse(localStorage.getItem('curentUser')).token,this.idCustomer).subscribe((user) =>{
    this.username = user.username;
    this.oldName = user.name;
    this.oldPassword = user.password;
    this.oldEmail = user.email;
    this.idCustomer = user.id;
  });

 
  console.log(this.isValid);

  }

  changeData(user){
    if(user.name != null){
      this.name = user.name;
    }
    if(user.password != null){
      this.password = user.password;
    }
    if(user.email != null){
      this.email = user.email;
    }
   
    
    console.log(this.idCustomer);
    
    
      
        
      
        this.dataService.changeUserData(this.idCustomer,this.username,this.password,this.name,this.email,JSON.parse(localStorage.getItem('curentUser')).token).subscribe((success) =>{
          console.log(success);
          if(success == true){
            this.errorMessage = "Successful data change";
            this.classAlert = "alertSuccessful";
            this.isValid = true;
          }else if(success == null){
            this.errorMessage = "Unsuccessful data change";
            this.classAlert = "alertUnsuccessful";
            this.isValid = true;
          }
          
         
         });
  }

}


interface User{
  id:number,
  username:string,
  password:string,
  name:string,
  email:string
}



