import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';
import { Globals } from '../../globals/globals';
import { Router } from '@angular/router';
import {UserToken} from '../login/login.component';

@Component({
    selector: 'app-admin-page',
  templateUrl: './admin-page.component.html',
  styleUrls: ['./admin-page.component.css']
})
export class AdminPageComponent implements OnInit {
  user:any;
  username:string;
  page: number = 1;
  users = [];
  checkForUsers:boolean = false;
  public customerId:number;
  

  constructor(private dataService:DataService,private router: Router) { }

  ngOnInit() {
    if(localStorage.getItem('curentUser') == null){
      this.router.navigate(['/login']);
    }else{
      if(JSON.parse(localStorage.getItem('curentUser')).user.permissions.includes('admin') == false){
        this.router.navigate(['/userPage']);
      }else{
        this.getUsers();
        
      }
    }
  
    
  
    
    
   
  }

  getUsers(){
    
    this.dataService.getUsers( JSON.parse(localStorage.getItem('curentUser')).token,this.page).subscribe((users) =>{
      this.users = users;
     
      if(this.users.length == 0){
        this.checkForUsers = false;
      }else{
        this.checkForUsers = true;
      }
       
      });
  }

  next(){
    this.page++;
    this.getUsers();

  }

  previous(){
    
    this.page--;
    this.getUsers();
    
  }

  redirectToChangeUserData(id:number){
    this.customerId = id;
    console.log(this.customerId);
    this.router.navigate(['/changeUserData',{id: this.customerId }]);
  }

  

}

interface User{
  id:number,
  username:string,
  name:string,
  surname:string,
  permissions:string[],
  email:string
}






