import { Component, OnInit } from '@angular/core';
import { Globals } from '../../globals/globals';
import { DataService } from '../../services/data.service';
import { Chart } from 'chart.js';
import {Observable} from 'rxjs';
import { forEach } from '@angular/router/src/utils/collection';
import { OnDestroy } from '@angular/core/src/metadata/lifecycle_hooks';
import {componentDestroyed} from "ng2-rx-componentdestroyed";
import { DecimalPipe } from '@angular/common';


@Component({
  selector: 'app-user-page',
  templateUrl: './user-page.component.html',
  styleUrls: ['./user-page.component.css']
})
export class UserPageComponent implements OnInit, OnDestroy {

readingsFromSensor:SensorReading[];

counter:number;
chartPh : Chart = [];
chartTemp : Chart = [];
chartHard : Chart = [];
chartChlo : Chart = [];
myObservable = Observable.interval(2000);
idOfSensor:number;
sensors:Sensor[];
timesPh: string[] = [];
amountsPh : number[] = [];
timesTemp: string[] = [];
amountsTemp : number[] = [];
timesHardness: string[] = [];
amountsHardness : number[] = [];
timesChlorine: string[] = [];
amountsChlorine : number[] = [];
lastReadingPhId:number;
lastReadingTempId:number;
lastReadingHardenssId:number;
lastReadingChlorineId:number;
dailyConsumption:number;
weeklyConsumption:number;
monthlyConsumption:number;
yearlyConsumption:number;
consumptionPrice:number = 0.002;
priceForDailyConsumption:number;
priceForWeeklyConsumption:number;
priceForMonthlyConsumption:number;
priceForYearlyConsumption:number;


  constructor(private dataService:DataService) { }

  ngOnDestroy(){
    
  }

  ngOnInit() {
    
    
    this.dataService.getAllSensors(JSON.parse(localStorage.getItem('curentUser')).token).subscribe((sensors) => {
    this.sensors = sensors;

     for(let sensor of sensors){
      if(sensor.type == 'PH'){

        this.dataService.getWaterParameters(JSON.parse(localStorage.getItem('curentUser')).token,sensor.id).subscribe((parameters) =>{
          let readings = parameters.reverse();
          this.lastReadingPhId = parameters[parameters.length - 1].id;
        for(let reading of readings){
          let jsdate = new Date(reading.time);
           this.timesPh.push(jsdate.toLocaleTimeString('en-BG',{hour:'numeric',minute:'numeric',second:'numeric'}));
           this.amountsPh.push(reading.amount);
         }
         this.chartPh = new Chart('ph',{
          type:'line',
          data: {
            labels: this.timesPh,
            datasets:[
              {
                data: this.amountsPh,
                backgroundColor: "rgba(71,192,255,0.4)",
                borderColor: '#47BEFF',
                borderHoverColor:"#0087D1",
                pointBoredColor:"#006DA8",
                pointHoverBackgroundColor:"#C4C4C4",
                pointHoverBorderColor:"#000000",
                pointBoredWidth:1
              }
            ]
          },
          options:{
            legend:{
              display: false
            },
            scales:{
              xAxes:[{
                display: true
              }],
              yAxes:[{
               display: true
              }]
            }
          }
        })
        })
        
      }

      if(sensor.type == 'TEMP'){

        this.dataService.getWaterParameters(JSON.parse(localStorage.getItem('curentUser')).token,sensor.id).subscribe((parameters) =>{
          let readings = parameters.reverse();
          this.lastReadingTempId = parameters[parameters.length - 1].id;
        for(let reading of readings){
          let jsdate = new Date(reading.time);
          
           this.timesTemp.push(jsdate.toLocaleTimeString('en-BG',{hour:'numeric',minute:'numeric',second:'numeric'}));
           this.amountsTemp.push(reading.amount);
         }
         this.chartTemp = new Chart('temp',{
          type:'bar',
          data: {
            labels: this.timesTemp,
            datasets:[
              {
                data: this.amountsTemp,
                backgroundColor: "rgba(4, 0, 242,0.4)",
                borderColor: '#47BEFF',
                
                pointBoredColor:"#006DA8",
                pointHoverBackgroundColor:"#C4C4C4",
                pointHoverBorderColor:"#000000",
                pointBoredWidth:1
              }
            ]
          },
          options:{
            legend:{
              display: false
            },
            scales:{
              xAxes:[{
                display: true
              }],
              yAxes:[{
               display: true
              }]
            }
          }
        })
        })
        
      }

      if(sensor.type == 'HARDNESS'){

        this.dataService.getWaterParameters(JSON.parse(localStorage.getItem('curentUser')).token,sensor.id).subscribe((parameters) =>{
          let readings = parameters.reverse();
          this.lastReadingHardenssId = parameters[parameters.length - 1].id;
        for(let reading of readings){
          let jsdate = new Date(reading.time);
          
           this.timesHardness.push(jsdate.toLocaleTimeString('en-BG',{hour:'numeric',minute:'numeric',second:'numeric'}));
           this.amountsHardness.push(reading.amount);
         }
         this.chartHard = new Chart('hard',{
          type:'radar',
          data: {
            labels: this.timesHardness,
            datasets:[
              {
                data: this.amountsHardness,
                backgroundColor: "rgba(204, 125, 0,0.4)",
                borderColor: '#efefef',
                borderHoverColor:"#0087D1",
                pointBoredColor:"#006DA8",
                pointHoverBackgroundColor:"#C4C4C4",
                pointHoverBorderColor:"#000000",
                pointBoredWidth:0.5
              }
            ]
          },
          options:{
            legend:{
              display: false
            },
            scales:{
              xAxes:[{
                display: false
              }],
              yAxes:[{
               display: false
              }]
            }
          }
        })    
        })
        
      }

      if(sensor.type == 'CHLORINE'){

        this.dataService.getWaterParameters(JSON.parse(localStorage.getItem('curentUser')).token,sensor.id).subscribe((parameters) =>{
          let readings = parameters.reverse();
          this.lastReadingChlorineId = parameters[parameters.length - 1].id;
        for(let reading of readings){
          let jsdate = new Date(reading.time);
          
           this.timesChlorine.push(jsdate.toLocaleTimeString('en-BG',{hour:'numeric',minute:'numeric',second:'numeric'}));
           this.amountsChlorine.push(reading.amount);
         }
         this.chartChlo = new Chart('chlo',{
          type:'bar',
          data: {
            labels: this.timesChlorine,
            datasets:[
              {
                data: this.amountsChlorine,
                backgroundColor: "rgba(255, 0, 12,0.4)",
                borderColor: '#47BEFF',
                
                pointBoredColor:"#006DA8",
                pointHoverBackgroundColor:"#C4C4C4",
                pointHoverBorderColor:"#000000",
                pointBoredWidth:1
              }
            ]
          },
          options:{
            legend:{
              display: false
            },
            scales:{
              xAxes:[{
                display: true
              }],
              yAxes:[{
               display: true
              }]
            }
          }
        })
        })
        
      }
    }
      this.myObservable.takeUntil(componentDestroyed(this)).subscribe(() =>
     this.refresh()
    );
    });

    //consumption table
    this.dataService.getConsumptionDaily(JSON.parse(localStorage.getItem('curentUser')).token).subscribe((value) =>{
      this.dailyConsumption = value;
      this.priceForDailyConsumption = value * this.consumptionPrice;
      
    });
    this.dataService.getConsumptionWeekly(JSON.parse(localStorage.getItem('curentUser')).token).subscribe((value) =>{
      this.weeklyConsumption = value;
      this.priceForWeeklyConsumption = value * this.consumptionPrice;
      
    });

    this.dataService.getConsumptionMonthly(JSON.parse(localStorage.getItem('curentUser')).token).subscribe((value) =>{
      this.monthlyConsumption = value;
      this.priceForMonthlyConsumption = value * this.consumptionPrice;
      
    });

    this.dataService.getConsumptionYearly(JSON.parse(localStorage.getItem('curentUser')).token).subscribe((value) =>{
      this.yearlyConsumption = value;
      this.priceForYearlyConsumption = value * this.consumptionPrice;
      
    });
    
  }

  refresh(){
  //  this.timesPh.push(this.timesPh[0])
  //  this.amountsPh.push(10)
  //  this.amountsPh.shift()
  //  this.timesPh.shift()
  //  this.chartPh.update()

  for(let sensor of this.sensors){
    if(sensor.type == 'PH'){
      this.dataService.getLastWaterParameters(JSON.parse(localStorage.getItem('curentUser')).token,sensor.id,this.lastReadingPhId).subscribe((parameters) =>{
        if(parameters.length == 0){
          return;
        }
        let readings = parameters.reverse();
        for(let i = 0; i < readings.length; i++){
          this.timesPh.shift();
          this.amountsPh.shift();
          this.amountsPh.push(readings[i].amount);
          let jsdate = new Date(readings[i].time);
          
           this.timesPh.push(jsdate.toLocaleTimeString('en-BG',{hour:'numeric',minute:'numeric',second:'numeric'}));
          
        }
        this.chartPh.update();
        this.lastReadingPhId = readings[readings.length - 1].id;
        
      })
    }
    if(sensor.type == 'TEMP'){
      this.dataService.getLastWaterParameters(JSON.parse(localStorage.getItem('curentUser')).token,sensor.id,this.lastReadingTempId).subscribe((parameters) =>{
        if(parameters.length == 0){
          return;
        }
        let readings2 = parameters.reverse();
        for(let i = 0; i < readings2.length; i++){
          this.timesTemp.shift();
          this.amountsTemp.shift();
          this.amountsTemp.push(readings2[i].amount);
          let jsdate = new Date(readings2[i].time);
          
           this.timesTemp.push(jsdate.toLocaleTimeString('en-BG',{hour:'numeric',minute:'numeric',second:'numeric'}));
          
        }
        this.chartTemp.update();
        this.lastReadingTempId = readings2[readings2.length - 1].id;
        
      })
    }
    if(sensor.type == 'HARDNESS'){
      this.dataService.getLastWaterParameters(JSON.parse(localStorage.getItem('curentUser')).token,sensor.id,this.lastReadingHardenssId).subscribe((parameters) =>{
        if(parameters.length == 0){
          return;
        }
        let readings = parameters.reverse();
        for(let i = 0; i < readings.length; i++){
          this.timesHardness.shift();
          this.amountsHardness.shift();
          this.amountsHardness.push(readings[i].amount);
          let jsdate = new Date(readings[i].time);
          
           this.timesHardness.push(jsdate.toLocaleTimeString('en-BG',{hour:'numeric',minute:'numeric',second:'numeric'}));
          
        }
        this.chartHard.update();
        this.lastReadingHardenssId = readings[readings.length - 1].id;
        
      })
    }
    if(sensor.type == 'CHLORINE'){
      this.dataService.getLastWaterParameters(JSON.parse(localStorage.getItem('curentUser')).token,sensor.id,this.lastReadingChlorineId).subscribe((parameters) =>{
        if(parameters.length == 0){
          return;
        }
        let readings = parameters.reverse();
        for(let i = 0; i < readings.length; i++){
          this.timesChlorine.shift();
          this.amountsChlorine.shift();
          this.amountsChlorine.push(readings[i].amount);
          let jsdate = new Date(readings[i].time);
          
           this.timesChlorine.push(jsdate.toLocaleTimeString('en-BG',{hour:'numeric',minute:'numeric',second:'numeric'}));
          
        }
        this.chartChlo.update();
        this.lastReadingChlorineId = readings[readings.length - 1].id;
        
      })
    }
  }
    
  }
}

interface Sensor{
  id:number,
  type:string
}


interface SensorReading{
  time:string,
  amount:number,
  id:number
}




