import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import {RouterModule, Routes} from '@angular/router';


import { AppComponent } from './app.component';
import { RegisterComponent } from './components/register/register.component';
import { DataService } from './services/data.service';
import { LoginComponent } from './components/login/login.component';
import { UserPageComponent } from './components/user-page/user-page.component';
import { AdminPageComponent } from './components/admin-page/admin-page.component';
import { ChangepasswordComponent } from './components/changepassword/changepassword.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { VerifyComponent } from './components/verify/verify.component';
import { UserinfoComponent } from './components/userinfo/userinfo.component';
import { HelpComponent } from './components/help/help.component';
import { AnimationScreenComponent } from './components/animation-screen/animation-screen.component';
import { ChangeUserDataComponent } from './components/change-user-data/change-user-data.component';
import { SocialLoginModule, AuthServiceConfig } from "angularx-social-login";
import { GoogleLoginProvider, FacebookLoginProvider } from "angularx-social-login";

let config = new AuthServiceConfig([
  {
    id: GoogleLoginProvider.PROVIDER_ID,
    provider: new GoogleLoginProvider("1086820069053-onfj0rb8tvv8ugt3ahjr05a9j8c8u99l.apps.googleusercontent.com")
  },
  {
    id: FacebookLoginProvider.PROVIDER_ID,
    provider: new FacebookLoginProvider("169092137053597")
  }
]);


@NgModule({
  declarations: [
    AppComponent,
    RegisterComponent,
    LoginComponent,
    UserPageComponent,
    AdminPageComponent,
    ChangepasswordComponent,
    NavbarComponent,
    VerifyComponent,
    UserinfoComponent,
    HelpComponent,
    AnimationScreenComponent,
    ChangeUserDataComponent
    
  ],
  imports: [
    BrowserModule,
    HttpModule,
    ReactiveFormsModule,
    FormsModule,
    SocialLoginModule.initialize(config),
    SocialLoginModule,
    RouterModule.forRoot([
      {path: '', redirectTo: 'animationPage', pathMatch: 'full'},
      {path:'register', component:RegisterComponent},
      {path:'login', component:LoginComponent},
      {path:'userPage', component:UserPageComponent},
      {path:'adminPage', component:AdminPageComponent},
      {path:'changePassword', component:ChangepasswordComponent},
      {path: 'users/:email/confirm/:token', component: VerifyComponent },
      {path:'userinfo', component:UserinfoComponent},
      {path:'help', component:HelpComponent},
      {path:'animationPage', component:AnimationScreenComponent},
      {path:'changeUserData', component:ChangeUserDataComponent}
    ])
  ],
  providers: [DataService],
  bootstrap: [AppComponent]
})
export class AppModule { }



