import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../../services/data.service';
import { NgModule } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Globals } from '../../globals/globals';
import { isError } from 'util';
import { matchOtherValidator } from './match-other-validator';

@Component({
  selector: 'app-changepassword',
  templateUrl: './changepassword.component.html',
  styleUrls: ['./changepassword.component.css']
})
export class ChangepasswordComponent implements OnInit {

  rForm: FormGroup;
  user: any;
  oldPassword: string;
  username: string = '';
  newPassword: string = '';
  newPasswordConfirm: string = '';
  token: string;
  passwordAlert: string = '';

  isSucess: boolean = false;


  constructor(private router: Router, private fb: FormBuilder, private dataService: DataService) {
    this.rForm = fb.group({
      'newPassword': [null, Validators.compose([Validators.required, Validators.minLength(6)])],
      'newPasswordConfirm': [null, Validators.compose([Validators.required, matchOtherValidator('newPassword')])],
      'oldPassword': [null, Validators.compose([Validators.required, Validators.minLength(6)])]


    })
  }

  ngOnInit() {

  }

  changePassword(user) {
    this.newPassword = user.newPassword;
    this.oldPassword = user.oldPassword;
    this.token = JSON.parse(localStorage.getItem('curentUser')).token;

    this.dataService.changePassword(this.token, this.oldPassword, this.newPassword).subscribe((user) => {


      if (user.confirmed != false && user.password == this.newPassword) {
        this.passwordAlert = 'Password is changed sucessfuly';
        this.isSucess = true;
        this.dataService.login(user.username, user.password).subscribe((usertoken) => {
          //this.usertoken = usertoken;
          this.token = usertoken.token;
          let isAdmin = usertoken.user.permissions.includes('admin');
          localStorage.setItem('curentUser', this.token);


          // console.log(this.usertoken.token);
          // let jeAdmin:boolean = false;
          // if(this.usertoken.user.permissions.includes('admin')){
          //   jeAdmin = true;
          // }

          // console.log("Je admin: " + jeAdmin);
          console.log(this.token);
          if (isAdmin) {
            console.log('User je admin');
            this.router.navigate(['/adminPage']);
          } else {
            console.log('User nije admin');
            this.router.navigate(['/userPage']);
          }



        });
      } else {
        this.passwordAlert = 'You did not enter correct old password';
        this.isSucess = true;
      }


    });
  }


  redirectToLogin() {
    this.router.navigate(['/login']);
  }

}





